import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import LoginBtn from './loginBtn';
import '../App.css';

import {Modal,Button } from 'react-bootstrap'




const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    width: 200,

  },
  selectEmpty: {
    marginTop: theme.spacing(1),
  },
}));


export default function NativeSelects() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    Location: "",
    name: '',
  });

  const handleChange = (event) => {
    const name = event.target.name;

    setState({
      ...state,
      [name]: event.target.value,
    });


  }
  
  return (


    <div className='nav'>
      <div className="contain-30">
        <div className="row">
          <div className="col-xl-3">
            <span className="OlxHeading">
              <h3>Olx</h3>
            </span>

            <FormControl variant="outlined" className={classes.formControl}>
              <InputLabel htmlFor="outlined-Location-native-simple">Location</InputLabel>

              <Select className="select"
                native
                value={state.Location}
                onChange={handleChange}
                label="Location"
                inputProps={{
                  name: 'Location',
                  id: 'outlined-Location-native-simple'
                }}
              >
                <option aria-label="none" value="" />
                <option value={'Karachi,Sindh'}>Karachi,Pakistan</option>
                <option value={'Lahore,Punjab'}>Lahore,Pakistan</option>
                <option value={'Quetta,KPK'}>Quetta,Pakistan</option>
              </Select>
            </FormControl>
          </div>
          <div className="col-xl-7 ">

            <div className="input-group mb-3">
              <input type="text" className="form-control searchBar" placeholder="Find Cars, Mobile Phones and more..." aria-label="Recipient's username" aria-describedby="button-addon2" />
              <div className="input-group-append">
                <button className="btn btn-dark" type="button" id="button-addon2"><i className="fa fa-search fa-2x"></i></button>
              </div>

            </div>


          </div>
          <div className="col-xl-2 ">
            <div className="navBtnsContainer">
            <LoginBtn/>
              <button className="sellBtn">
              
                <i className="fa fa-plus"></i>
              Sell</button>
            </div>
          </div>
        </div>
      </div>
    </div>

  );
}