import React from 'react'
import '../App.css'
import car from '../Images/car.jpg'
import watch from '../Images/watch.jpg'

class Cards extends React.Component {

    constructor() {
        super()
        this.state = {
            products: [{ id: 1, name: 'Honda Civic', price: 3000000, image:  car , status: 'new' },
            { id: 2, name: 'Bristtle', price: 300, image: watch , status: 'hot' },
            { id: 3, name: 'Wrist Watch', price: 400, image: watch, status: 'new' },
            { id: 4, name: 'Car', price: 4000000, image: car, status: 'hot' },
            { id: 5, name: 'Chairs', price: 1000, image: car , status: 'new' },
            { id: 6, name: 'House', price: 10000000, image: car , status: 'hot' }],
            value: ''

        }
        // this.DIV= React.createRef();
        
    }


   

    render() {
       // const besideDiv = this.DIV.current;


        return (


            <React.Fragment>
                {/* {
                console.log(besideDiv)
                } */}
                {
                    this.state.products.map((value, index) => {
                        return <div className='card' id={index}>
                            {/* //ref={this.DIV} */}
                                <h6 >{value.name}</h6>
                            <img src={value.image} alt="Product" />
                            {(this.state.products[index].status === 'hot') ?
                                <div>
                                    <div className="beside Line" id={`ID`+this.state.products[index].id}>

                                        <p className='price'>{`Rs. ` + value.price}<br />

                                            <p className='description'>{this.props.description}</p>
                                            <span className='locationName'>

                                                {this.props.LocationName}</span>
                                            <span className='date'>{this.props.date}</span></p>

                                        {/* <button className='cardBtn'>Add to Cart</button> */}
                                    </div>
                                    <span className='statusSpan'>{value.status}</span>
                                    
                                </div>
                                :
                                <div>

                                    <div className="beside" id="besideDIV">

                                        <p className='price'>{`Rs. ` + value.price}<br />

                                            <p className='description'>{this.props.description}</p>
                                            <span className='locationName'>

                                                {this.props.LocationName}</span>
                                            <span className='date'>{this.props.date}</span></p>

                                        {/* <button className='cardBtn'>Add to Cart</button> */}
                                    </div>
                                    <span className='statusSpan hide'>{value.status}</span>


                                </div>
                            }

                          

                        </div>
                    })
                }


            </React.Fragment>
        )
    }


}
export default Cards;